* {
  box-sizing: border-box;
  background-color: #edeff2;
}

.knowbotlogo{
  height: auto;
  width: 8vh;
  padding: 1vh;
  background-color: #ffffff;
}

.logo{
  background-color: #ffffff;
  display: block;
  text-align: center;
}

.logoimg{
  height: auto;
  width: 50%;
  padding: 1vh;
  background-color: #ffffff;
}

.headerfont {
  font-family: 'Montserrat Alternates';
  font-size:2.3em;
  font-weight: bold;
  color: #7bc143;
  text-align: center;
  padding-top: 5px;
  padding-bottom: 5px;
  width:80%;
}

.white{
  background-color: #ffffff;
}

.green{
  background-color: #7bc143;
}

.ellipsislogo{
  padding-top: 2%;
  width: 2vh;
  height: auto;
  background-color: #ffffff;
}

.header-row{
  height: 10%;
  background-color: #ffffff;
  padding-top: 5px;
  padding-bottom: 5px;
}

.clock-img{
  background-color: #7bc143;
  height: 1.5em;
}
.timer{
  background-color: #7bc143;
  text-align: left;
  font-size:1.2em;
  color: #ffffff;
}

.info-text{
  font-family: "Lucida Console", Monaco, monospace;
  background-color: #7bc143;
  color: #ffffff;
  font-size:20px;
  display: block;
  text-align: center;
  padding: 1vh;

}

.title{
  font-family: 'Montserrat Alternates';
  font-size:1.5em;
  font-weight: bold;
  color:  #ffffff;
  display: block;
  text-align: center;
  background-color: #7bc143;
}

.intro{
  font-family: 'Montserrat Alternates';
  font-size:1.2em;
  font-weight: bold;
  display: block;
  text-align: left;
  padding: 1vh;
  margin-left: auto;
  background-color: #ffffff;
  height: 65vh;
  overflow-y: scroll;
}

ul, li{
  padding-top: 1vh;
  background-color: #ffffff;
}

.start-button{
  font-size:28px;
  color: #ffffff;
  background-color: #7bc143;
  display: block;
  width: 100%;
  height: auto;
  bottom: 5%;
  position: relative;
  font-family: "Lucida Console", Monaco, monospace;
  font-weight: bold;
}

.display-row{
  width: 100%;
  height: 10vh;
}

.ellipsisbutton{
  width: 10%;
  height: auto;
  background-color:#ffffff;
}
