.content-label{
  background-color: #ffffff;
  font-size:20px;
  height: 75vh;
  text-align: justify;
  padding: 1vh;
  user-select: none;
  overflow-x: hidden;
  overflow-y: scroll;

}

.read-button{
  font-size:24px;
  color: #ffffff;
  background-color: #7bc143;
  display: block;
  width: 100%;
  height: auto;
  bottom: 5%;
  position: relative;
  font-family: "Lucida Console", Monaco, monospace;
}

.input{
  background-color: #ffffff;
  height: 75vh;
  width: 100%;
  overflow-y: scroll;
  font-size:1.2em;

}

.score{
  background-color: #ffffff;
  font-size:24px;
  height: 60vh;
  text-align: center;
  display: block;
}

.score-label{
  font-family: "Lucida Console", Monaco, monospace;
  background-color: #ffffff;
  display: block;
  text-align: right;
  padding: 1vh;
}

.score-value{
  font-family: "Lucida Console", Monaco, monospace;
  background-color: #ffffff;
  display: block;
  text-align: left;
  padding: 1vh;
}
